import { Component, OnInit, NgZone, Inject} from "@angular/core";

const template = require("./app.component.html");

import { VisitModel, WhoIsWho, TypeEnum } from '../model/visitModel'
import { OfficeDataProvider } from "../service/officeDataProvider";

@Component({
  selector: "app-home",
  template
})
export default class AppComponent {
  constructor(
    @Inject(NgZone)private zone: NgZone, 
    @Inject(OfficeDataProvider) private officeDataProvider: OfficeDataProvider) {
  }

  public model: VisitModel = null;
  public errorMessage: String;

  ngOnInit() {
    this.getModel();
  }

  private getModel() { 
    var that = this;
    this.officeDataProvider.getModel(
      function(newModel : VisitModel, error: string) {
      that.zone.run(() => {
        if (error){
            this.reportError(error);
        } else {
            if (that.model == null) {
              that.model = newModel;
              return;
            }
            
            that.officeDataProvider.mergeModels(that.model, newModel);
          }
        }
      );
    });
  }

  public setNormal(email: string) {
    var setTrustedOn = this.model.WhoIsWhoList.find(element => element.email == email);
    if (setTrustedOn != null) {
      setTrustedOn.type = TypeEnum.Normal;
    }
  }
  
  public setTrusted(email: string) {
    var setTrustedOn = this.model.WhoIsWhoList.find(element => element.email == email);
    if (setTrustedOn != null) {
      setTrustedOn.type = TypeEnum.Trusted;
    }
  }

  private refresh() {
    this.getModel();
  }

  private reportError(message: string) {
    this.errorMessage = message;
  }
}
