import { Injectable } from '@angular/core';
import { VisitModel, WhoIsWho, TypeEnum } from '../model/visitModel';
import { m } from '@angular/core/src/render3';

@Injectable()
export class OfficeDataProvider {
  constructor() { }

  public getModel(onFetchData) : VisitModel {
    var item = Office.context.mailbox.item;
    let model = new VisitModel();
    model.room = item.location;
    model.userName = Office.context.mailbox.userProfile.displayName;

    item.to.getAsync(function (asyncResult) {
      model.WhoIsWhoList = asyncResult.value.map(element=> <WhoIsWho> {
        email: element.emailAddress,
        plateNumber: "",
        type: TypeEnum.Normal
      });

      if (onFetchData != null) {
        if (asyncResult.status == Office.AsyncResultStatus.Failed) {
          onFetchData(null, asyncResult.error.message);
        } else {
          onFetchData(model);
        }
      }
    });

    return model;
  }
  public mergeModels(model:VisitModel, newModel: VisitModel) {
    model.room = newModel.room;
    model.userName = newModel.userName;

    // Remove emails non-existing in new who is who list.
    let elementIndex = 0;
    model.WhoIsWhoList.slice().forEach(element => {
      let existingModelIndex = newModel.WhoIsWhoList.findIndex(existing => { 
        return existing.email == element.email 
      });
      if (existingModelIndex < 0) {
        model.WhoIsWhoList = model.WhoIsWhoList.splice(elementIndex, 1);
      }
      elementIndex ++;
    })

    // Add new emails to who is who list
    newModel.WhoIsWhoList.forEach(element => {
      let existingModel = model.WhoIsWhoList.find(existing => { 
        return existing.email == element.email 
      });

      if (existingModel == null) {
        model.WhoIsWhoList.push(element);
      }
    });
  }
  
}